@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html,
body {
	height: 100%;
}

body {
	font-family: Roboto, sans-serif;
	font-size: 18px;
}

#root {
	height: 100%;
}

a {
	text-decoration: none;
}

a,
a:visited,
a:hover,
a:active {
	color: inherit;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

button {
	border: none;
}

.container {
	width: 100%;
	height: 100%;
	margin: 0 auto;
}

@media (max-width: 767.98px) {
	.container {
		max-width: none;
		padding: 0 10px;
	}
}

.app {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	background-color: #0079bf;
}

.header {
	background-color: #0067a3;
}

.header-body {
	height: 55px;
	padding: 0 15px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.logo {
	font-size: 28px;
	color: #fff;
}

.header-link {
	cursor: default;
}

.user-menu {
	display: flex;
	align-items: center;
	cursor: pointer;
	position: relative;
}

.toggle-menu {
	background-color: #fff;
	width: 134px;
	border-radius: 5px;
	position: absolute;
	top: 50px;
	right: 4px;
	font-size: 14px;
}

.toggle-menu::before {
	content: '';
	width: 11px;
	height: 11px;
	transform: rotate(45deg);
	position: absolute;
	top: -6px;
	left: 89px;
	background-color: #fff;
}

.toggle-menu-list {
	text-align: center;
	position: relative;
	z-index: 1;
}

.toggle-menu-item {
	border-radius: 5px;
	padding: 7px 0;
}

.toggle-menu-item:hover {
	background-color: #dedede;
}

.arrow-down {
	transform: rotate(180deg);
}

.main {
	background-color: #0079bf;
	flex: 1 1 auto;
	padding-bottom: 55px;
}

.main-body {
   display: flex;
	justify-content: space-between;
	padding: 0 4px;
}

.task-container {
   padding: 22px 11px;
	flex: 0 1 25%;
}

.task-block {
   background-color: #e4e5e9;
	border-radius: 8px;
	padding: 13px;
}

.block-title {
   margin-bottom: 10px;
}

.block-input {
   border: none;
	border-radius: 5px;
	padding: 7px;
	width: 100%;
	display: block;
	margin: 0px 0px 15px 0px;
	outline: none;
}

.block-submit-btn {
   cursor: pointer;
	color: #fff;
	background-color: #0079bf;
	border-radius: 5px;
	border: none;
	outline: none;
	padding: 7px 20px;
}

.block-empty {
   border-radius: 5px;
	margin: 15px 0px;
	height: 33px;
	background: url(/kanban_board/static/media/task-arrow-down.157e19ef.svg) 98% 50% no-repeat;
	background-color: #fff;
	cursor: pointer;
}

.card {
   background-color: #fff;
	border-radius: 5px;
	margin: 15px 0px;
	padding: 8px;
}

.card-passed {
   margin: 0px 13px;
	cursor: pointer;
	transition: all 0.2s ease;
	border-radius: 0px;
}

.card-passed:hover {
   background-color: #dedede;
}

.card-passed:first-child {
   margin-top: -25px;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}

.card-passed:last-child {
   border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
}

.add-card {
   display: inline-block;
	color: #5e6c84;
	background: url(/kanban_board/static/media/add-card.c6cdbf7a.svg) 0 9px no-repeat;
	cursor: pointer;
	background-color: transparent;
	padding: 8px 8px 8px 18px;
	border-radius: 5px;
	transition: all 0.2s ease;
}

.add-card:hover {
   background-color: #fff;
}

@media (max-width: 830px) {
   .main {
      padding-bottom: 85px;
   }
   .main-body {
      flex-direction: column;
   }
}
.footer {
	background-color: #0067a3;
	height: 55px;
	position: fixed;
	bottom: 0;
	width: 100%;
}

.footer-body {
	color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px;
}

.tasks {
	display: flex;
	justify-content: space-between;
}

.active-tasks {
   margin: 0px 36px 0px 0px;
}

@media (max-width: 830px) {
   .footer {
      height: 85px;
   }
   .footer-body {
      flex-direction: column;
   }
   .tasks {
      margin: 0px 0px 15px 0px;
   }
   .kanban-author {
      font-size: 14px;
   }
}

@media (max-width: 350px) {
   .tasks {
      font-size: 16px;
   }
}
.task-page-container {
	background-color: #fff;
	margin: 15px 15px 73px;
	border-radius: 5px;
	padding: 22px 28px;
	position: relative;
}

.task-page-title {
	font-size: 24px;
	font-weight: 400;
	margin-bottom: 40px;
}

.task-page-tasks {
	margin-bottom: 35px;
}

.task-page-task {
	margin-bottom: 10px;
}

.task-page-date {
	margin-top: 10px;
	margin-bottom: 10px;
	color: #888888;
	font-size: 14px;
}

.task-page-info {
	margin-bottom: 10px;
	font-style: italic;
	color: #888888;
}

.task-page-button {
	background-color: #0079bf;
	color: #fff;
	cursor: pointer;
	padding: 5px 20px;
	border-radius: 5px;
	margin-right: 10px;
}

@media (max-width: 830px) {
	.task-page-container {
		margin: 15px 15px 100px;
	}
}

.page-exit span:first-child {
	position: absolute;
	background-color: #000;
	height: 1px;
	width: 32px;
	top: 28px;
	right: 22px;
	transform: rotate(45deg);
	cursor: pointer;
}

.page-exit span:nth-child(2) {
	position: absolute;
	background-color: #000;
	height: 1px;
	width: 32px;
	top: 28px;
	right: 22px;
	transform: rotate(135deg);
	cursor: pointer;
}
